td {
  width: 105px;
  height: 105px;
  border: 3px solid #A7A37E;
  border-collapse: collapse;
  text-align: center;
  vertical-align: middle;
  font-size: 66px;
  font-family: 'Indie Flower', cursive;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer; }

#cell-0 {
  border-left: none;
  border-top: none; }

#cell-1 {
  border-top: none; }

#cell-2 {
  border-top: none;
  border-right: none; }

#cell-3 {
  border-left: none; }

#cell-5 {
  border-right: none; }

#cell-6 {
  border-left: none;
  border-bottom: none; }

#cell-7 {
  border-bottom: none; }

#cell-8 {
  border-right: none;
  border-bottom: none; }

.x-marker {
  color: #046380; }

.o-marker {
  color: #002F2F; }



